@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700i,800,800i&display=swap);
body {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.toolbar {
  /* position: fixed; */
  top: 0;
  left: 0;
  width: 100%;
  background: #393e46;
  height: 56px;
}

.toolbar__navigation {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
}

.toolbar__logo a img {
  color: white;
  text-decoration: none;
  height: 40px;
  border-radius: 50%;
}

.spacer {
  flex: 1 1;
}

.toolbar_navigation_items a {
  color: #f0ece2;
  text-decoration: none;
}

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active {
  color: #acdbdf;
}

.toolbar_navigation_items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation_items li {
  padding: 0 0.5rem;
  list-style: none;
}

.disabled {
  pointer-events: none;
  color: grey;
}

.disabled:hover,
.disabled:active {
  color: grey;
}

@media (max-width: 768px) {
  .toolbar_navigation_items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 30px;
  height: 2px;
  background: white;
}
.resumeDiv {
  max-width: 900px;
  margin: 0 auto;
  padding: 24px;
  color: #f0ece2;
}

hr.section {
  height: 30px;
  border-style: solid;
  border-color: #c83232;
  border-width: 1px 0 0 0;
  border-radius: 20px;
}

hr.section:before {
  display: block;
  content: "";
  height: 30px;
  margin-top: -31px;
  border-style: solid;
  border-color: #c83232;
  border-width: 0 0 1px 0;
  border-radius: 20px;
}

/* hr.experience-separator {

} */

hr.experience-separator {
  border: 0;
  height: 0; /* Firefox... */
  box-shadow: 0 0 10px 1px #c83232;
}
hr.experience-separator:after {  /* Not really supposed to work, but does */
  content: "\00a0";  /* Prevent margin collapse */
}

span#address {
