.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.box {
  position: relative;
  width: 300px;
  height: 400px;
  background: #f0ece2;
  box-shadow: 0 30px 30px rgba(0, 0, 0, 0.5);
}

.box .imgBx {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.box .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.box:hover .imgBx img,
.box:active .imgBx img {
  opacity: 0;
}

.box .content {
  position: absolute;
  bottom: 20px;
  left: 10%;
  width: 80%;
  height: 60px;
  background: #f0ece2;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  overflow: hidden;

  padding: 15px;
  box-sizing: border-box;
}

.box:hover .content,
.box:active .content {
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  color: black;
}

.box .content h3 {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

.box .content p {
  margin: 10px 0 0;
  padding: 0;
  opacity: 0;
  line-height: 1.2em;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-align: justify;
}

.box:hover .content p,
.box:active .content p {
  opacity: 1;
  -webkit-transition-delay: 0.5s;
          transition-delay: 0.5s;
}

