#contact {
  /* display: flex;
  justify-content: center; */
  box-sizing: border-box;
  /* background: #f0ece2; */
  background: #393e46;
  color: #393e46;
  line-height: 1.6;
  padding: 1em;
  max-width: 95%;
  margin: 5px auto;
  /* margin-right: auto; */
}

div.contactWrapper {
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.2);
}

div.contactWrapper > * {
  padding: 1em;
}

div.myInfo {
  background: #69779b;
  color: #f0ece2;
}

#contact div div ul li {
  list-style: none;
}

div.myInfo h5 {
  text-align: center;
  margin: 0 0 1rem 0;
}

div.myInfo ul {
  text-align: center;
  margin: 0 0 1rem 0;
  padding: 10px;
}

div.contactForm {
  background: #f0ece2;
}

div.contactForm form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

div.contactForm form label {
  display: block;
}

div.contactForm form p {
  margin: 0;
}

div.contactForm form .full {
  grid-column: 1 / 3;
}

div.contactForm form button,
div.contactForm form input {
  width: 80%;
  padding: 1em;
  border: 1px solid #69779b;
}

div.contactForm form textarea {
  width: 90%;
  padding: 1em;
  border: 1px solid #69779b;
}

div.contactForm form button {
  background: #69779b;
  color: #f0ece2;
  border: 0;
  text-transform: uppercase;
  border-radius: 6px;
}

div.contactForm form button:hover,
div.contactForm form button:focus {
  background: #f0ece2;
  color: #c83232;
  outline: 0;
  -webkit-transition: background-color 2s ease-out;
  transition: background-color 2s ease-out;
}

.PSA {
